<template>
    <div class="main">
        <div>
            <MdlButton v-on:click.native="onSwitchMainState('converter')" type="button" class="mdl-js-ripple-effect">
                Converter
            </MdlButton>
            <MdlButton v-on:click.native="onSwitchMainState('favourites')" type="button" class="mdl-js-ripple-effect">
                Favourites
            </MdlButton>
        </div>
        <div :is="mainState"></div>
    </div>
</template>

<script>
    import { MdlButton } from 'vue-mdl'
    import Converter from '../components/Converter.vue'
    import Favourites from '../components/Favourites.vue'

    export default {
        name: "Main",
        data: () => ({
            mainState: Converter
        }),
        components: {
            'converter': Converter,
            'favourites': Favourites,
            MdlButton
        },
        methods: {
            onSwitchMainState: function(stateName) {
                this.mainState = stateName
            }
        },
    }
</script>

<style scoped>
    .main {
        background: white;
        margin: 0 auto;
        margin-top: 32px;
        padding: 16px;
        max-width: 800px;
    }

</style>