<template>
    <div class="register mdl-shadow--2dp">
        <div class="mdl-typography--headline">Register</div>
        <form action="#">
            <MdlTextfield floating-label="Username" v-model="username" type="text"></MdlTextfield>
            <MdlTextfield floating-label="Password" v-model="password" type="password"></MdlTextfield>
            <div class="error">{{ errorMessage }}</div>
            <div class="buttons">
                <!-- Colored raised button -->
                <MdlButton v-on:click.native="this.startRegister" type="button" colored raised class="mdl-js-ripple-effect">
                    Register
                </MdlButton>
                <!-- Flat button with ripple -->
                <MdlButton v-on:click.native="$emit('switchState', 'login')" type="button" class="mdl-js-ripple-effect">
                    Login
                </MdlButton>
            </div>
        </form>
    </div>
</template>

<script>
    import { MdlButton, MdlTextfield } from 'vue-mdl'
    import axios from 'axios'

    export default {
        name: "Register",
        data: () => ({
            username: "",
            password: "",
            errorMessage: "",
        }),
        methods: {
            startRegister: function (username, password) {
                let data = this.$data;
                axios.get('http://easyconvert.azurewebsites.net/register/register?username=' + username + "&password=" + password)
                    .then(response => {
                        if (response.data.success) {
                            data.errorMessage = 'Succesfully registered!';
                        } else {
                            data.errorMessage = 'Failed to register';
                        }
                    })
                    .catch((response) => {
                        console.log(response);
                        data.errorMessage = 'Failed to register';
                    })
            }
        },
        components: {
            MdlButton,
            MdlTextfield
        }
    }
</script>

<style scoped>
    .register {
        background: white;
        margin: 0 auto;
        margin-top: 32px;
        padding: 16px;
        max-width: 500px;
    }
    .register form {
        text-align: center;
    }
    .buttons {
        text-align: center;
    }
    .buttons button {
        margin: 0 10px;
    }
    .error {
        color: red;
        padding: 16px;
    }
</style>