<template>
    <div class="login mdl-shadow--2dp">
        <div class="mdl-typography--headline">Login</div>
        <form action="#">
            <MdlTextfield floating-label="Username" v-model="username" type="text"></MdlTextfield>
            <MdlTextfield floating-label="Password" v-model="password" type="password"></MdlTextfield>
            <div class="error">{{ errorMessage }}</div>
            <div class="buttons">
                <!-- Colored raised button -->
                <MdlButton v-on:click.native="startLogin" type="button" colored raised class="mdl-js-ripple-effect">
                    Login
                </MdlButton>
                <!-- Flat button with ripple -->
                <MdlButton v-on:click.native="$emit('switchState', 'register')" type="button" class="mdl-js-ripple-effect">
                    Register
                </MdlButton>
            </div>
        </form>
    </div>
</template>

<script>
    import { MdlButton, MdlTextfield } from 'vue-mdl'
    import axios from 'axios'

    export default {
        name: "Login",
        data: () => ({
            username: "",
            password: "",
            errorMessage: "",
        }),
        methods: {
            startLogin: function (username, password) {
                let data = this.$data;
                axios.get('http://easyconvert.azurewebsites.net/auth/createToken?username=' + username + "&password=" + password)
                    .then(response => {
                        if (response.data.token) {
                            axios.get('http://easyconvert.azurewebsites.net', {
                                headers: { 'Authorization': 'Bearer ' + response.data.token }
                            }).then(() => {
                                this.$emit('switchState', 'ma')
                            })
                        } else {
                            data.errorMessage = 'Failed to login';
                        }
                    })
                    .catch(() => {
                        data.errorMessage = 'Failed to login';
                    })
            }
        },
        components: {
            MdlButton,
            MdlTextfield
        }
    }
</script>

<style scoped>
    .login {
        background: white;
        margin: 0 auto;
        margin-top: 32px;
        padding: 16px;
        max-width: 500px;
    }
    .login form {
        text-align: center;
    }
    .buttons {
        text-align: center;
    }
    .buttons button {
        margin: 0 10px;
    }
    .error {
        color: red;
        padding: 16px;
    }
</style>