<template >
  <div id="app">
    <div class="demo-layout-transparent mdl-layout mdl-js-layout mdl-layout--fixed-header">
      <Header></Header>
      <main class="mdl-layout__content">
        <div :is="currentState" v-on:switchState="onSwitchState"></div>
      </main>
      <Footer></Footer>
    </div>
  </div>
</template>

<script>
  import componentHandler from 'material-design-lite/material.min.js'
  import Login from './components/Login.vue'
  import Register from './components/Register.vue'
  import Header from './components/Header.vue'
  import Footer from './components/Footer.vue'
  import Main from './components/Main.vue'

export default {
  name: 'app',
  data: () => ({
    currentState: 'login'
  }),
  components: {
    'login': Login,
    'register': Register,
    'ma': Main,
    Header,
    Footer
  },
  methods: {
    onSwitchState: function(stateName) {
      this.currentState = stateName
    }
  },
  created() {
    this.$nextTick(() => {
      componentHandler.upgradeDom();
      componentHandler.upgradeAllRegistered();
    });
  }
}
</script>

<style>
  body {
    padding: 0;
    margin: 0;
  }

  html, body {
    overflow:hidden;
  }

  .demo-layout-transparent {
    background: url('assets/bg.jpg') center / cover;
  }
  .demo-layout-transparent .mdl-layout__header,
  .demo-layout-transparent .mdl-layout__drawer-button {
    /* This background is dark, so we set text to white. Use 87% black instead if
       your background is light. */
    color: white;
  }
</style>
