<template>
    <div>
        <div>
            <MdlTextfield v-model="currentFromValue" v-on:input="currentToValue = convert(currentFrom, currentTo, currentFromValue)" label="From" type="text" pattern="[0-9]*(\.[0-9]+)?"></MdlTextfield>
            <MdlSelect label="Currency from" v-model="currentFrom" id="currencyFrom" :options="currencies"></MdlSelect>
        </div>
        <div>
            <MdlTextfield v-bind:value="convert(currentFrom, currentTo, currentFromValue)" label="To" type="text" pattern="[0-9]*(\.[0-9]+)?" readonly></MdlTextfield>
            <MdlSelect label="Currency from" v-model="currentTo" id="currencyTo" :options="currencies"></MdlSelect>
            <MdlButton v-on:click.native="$emit('switchState', 'register')" type="button" class="mdl-js-ripple-effect">
                Add to favourite
            </MdlButton>
        </div>
    </div>
</template>

<script>
    import { MdlTextfield, MdlSelect, MdlButton } from 'vue-mdl'

    export default {
        name: "Converter",
        data: () => ({
            currentFrom: 'USD',
            currentTo: 'UAH',
            currentFromValue: 0,
            currentToValue: 0,
            currencies: [
                'UAH', 'USD', 'BTC', "EUR"
            ],
            rates: {
                'USD-UAH': 26.1,
                'UAH-USD': 1 / 26.1,
                'USD-BTC': 1 / 7694.08,
                'BTC-USD': 7694.08,
                'UAH-BTC': 1 / (7694.08 * 27.2123),
                'BTC-UAH': 7694.08 * 26.1,
                'USD-EUR': 1 / 0.8548,
                'EUR-USD': 0.8548,
                'UAH-EUR': 1 / (0.8548 * 26.1),
                'EUR-UAH': 0.8548 * 26.1,
                'BTC-EUR': 7694.08 / 0.8548,
                'EUR-BTC': 1 / (7694.08 / 0.8548),
            }
        }),
        methods: {
            convert: function(from, to, amount) {
                if (from == to) return amount;
                console.log("Convert");
                return amount * this.$data.rates[from + '-' + to];
            }
        },
        components: {
            MdlTextfield, MdlSelect, MdlButton
        }
    }
</script>

